import { NavLink } from "react-router-dom";
import styled from "styled-components";

export const AdminSidebarContainer = styled.div`
  width: 100%;
  max-width: 300px;
  min-height: calc(100vh - 54px);
  padding: 14px 0 14px 16px;
  border-right: 1px solid ${({ theme }) => theme.contrastColor};

  @media (max-width: 900px) {
    max-width: 260px;
  }

  @media (max-width: 768px) {
    padding: 8px 16px;
    min-height: 60px;
    max-width: 100%;
    border-right: none;
    border-bottom: 1px solid ${({ theme }) => theme.contrastColor};
  }
`;

export const AdminSidebarTitle = styled.div`
  color: ${({ theme }) => theme.textColor};
  font-size: 25px;
  font-weight: 700;
  filter: opacity(0.6);

  @media (max-width: 768px) {
    margin-left: 0;
  }
`;

export const AdminSidebarMenu = styled.div`
  display: flex;
  flex-direction: column;
  gap: 2px;
  margin-top: 12px;
  padding-right: 8px;

  @media (max-width: 768px) {
    flex-direction: row;
    overflow: hidden;
    overflow-x: auto;
    gap: 10px;
  }
`;

export const AdminMenuItem = styled(NavLink)`
  display: flex;
  align-items: center;
  padding: 12px 8px;
  border-radius: 5px;
  min-width: 240px;
  color: ${({ theme }) => theme.textColor};
  text-decoration: none;
  font-weight: 500;
  transition: all 0.2s ease;
  cursor: pointer;
  user-select: none;

  & > .iconify {
    font-size: 26px;
  }

  & > span {
    margin-left: 8px;
  }

  &.active {
    background: ${({ theme }) => theme.hoverColor};
  }

  &:hover {
    background: ${({ theme }) => theme.hoverColor};
  }

  @media (max-width: 768px) {
    border: 1px solid ${({ theme }) => theme.hoverColor};
  }
`;
